html {
  position: absolute;
  inset: 0px;
}

body {
  margin: 0px;
  color: #dde;
  background-color: #223;
  font-family: Arial, Helvetica, sans-serif, "Material Symbols Outlined";
}

nav {
  background-color: #112;
  display: flex;
  justify-content: center;
  position: fixed;
  inset: 0px;
  bottom: unset;
  z-index: 2;
}
nav div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 20px;
  height: 70px;
  max-width: 1250px;
  flex-grow: 1;
}
nav div * {
  margin: 0px;
}
nav div a {
  text-decoration: none;
  color: #dde;
}
nav div span:first-child a {
  display: flex;
  align-items: center;
}
nav div span:first-child a img {
  height: 42px;
  margin: 0px 7px;
}
nav div span:first-child a h1 {
  margin: 0px 7px;
  position: absolute;
  top: 20px;
}
nav div span:first-child a .swipe {
  position: absolute;
  top: 20px;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  transition: width 150ms ease-out;
  width: 0px;
  z-index: 1;
  background-color: #112;
}
nav div span:first-child a .swipe h1 {
  margin-right: 0px;
  background: linear-gradient(90deg, #fc4, #f76);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  width: 305px;
  position: unset;
}
nav div span:first-child a:hover span.swipe {
  width: 305px;
}
nav div span:last-child .full {
  display: flex;
}
nav div span:last-child .full a {
  padding: 12px 17px;
  margin-left: 7px;
  border-radius: 10px;
  background-color: #112;
  border: #334 2px solid;
  transition: background-color 70ms ease-out;
}
nav div span:last-child .overflow {
  display: none;
}
nav div span:last-child button.overflow {
  color: #dde;
  padding: 12px 17px;
  margin-left: 7px;
  border-radius: 10px;
  background-color: #112;
  border: #334 2px solid;
  transition: background-color 70ms ease-out;
  cursor: pointer;
}
nav div span:last-child button.overflow.enabled {
  background-color: #bbc;
  border: transparent 2px solid;
  color: #223;
}
nav div span:last-child span.overflow {
  position: absolute;
  top: 70px;
  left: 0px;
  right: 0px;
  display: none;
  align-items: stretch;
  justify-content: flex-start;
  flex-direction: column;
  text-align: right;
  background-color: #112;
  max-height: 0px;
  transition: max-height 300ms ease-out;
  overflow: hidden;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}
nav div span:last-child span.overflow.enabled {
  max-height: 252px;
}
nav div span:last-child span.overflow a {
  padding: 12px 20px;
  transition: background 70ms ease-out;
  border-radius: 12px;
}
nav div span:last-child span.overflow a:nth-child(odd) {
  background-color: #080818;
}

main {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-top: 70px;
  padding: 20px;
}
main .container {
  max-width: 1250px;
  flex-grow: 1;
}
main .container div {
  background-color: #334;
  margin-bottom: 20px;
  padding: 20px;
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
main .container div:last-child {
  margin-bottom: 0px;
}
main .container div h1 {
  margin: 0px;
  position: absolute;
  top: 10px;
  right: 10px;
  color: #556;
  font-size: 2em;
}
main .container div .img {
  width: 35%;
  min-width: 100px;
  background-position: center;
  aspect-ratio: 16/9;
  background-size: cover;
  border-radius: 10px;
}
main .container div .info {
  color: #dde;
  text-decoration: none;
  flex-grow: 1;
  margin-left: 20px;
  z-index: 1;
}
main .container div .info h2 {
  color: #eef;
  font-size: 2em;
  margin: 0px;
  margin-bottom: 10px;
}
main .container div .info p {
  font-size: 1.5em;
  margin: 0px;
}

@media screen and (max-width: 1012px) {
  nav .full {
    display: none !important;
  }
  nav .overflow {
    display: flex !important;
  }
}
@media screen and (max-width: 461px) {
  nav span:first-child span {
    display: none !important;
  }
}
@media screen and (max-width: 700px) {
  main h1 {
    font-size: 1.5em !important;
  }
  main .info h2 {
    font-size: 1.2em !important;
  }
  main .info p {
    font-size: 1em !important;
  }
}
body:not(.touch) nav button.overflow:hover, body:not(.touch) nav .full a:hover {
  background-color: #eef !important;
  border: transparent 2px solid;
  color: #223;
}
body:not(.touch) nav span.overflow a:hover {
  background-color: #dde !important;
  color: #223;
}

body.touch nav button.overflow:active, body.touch nav .full a:active {
  background-color: #eef !important;
  border: transparent 2px solid;
  color: #223;
}
body.touch nav span.overflow a:active {
  background-color: #dde !important;
  color: #223;
}